import React ,{useEffect,useState} from 'react'
import { useTotalSupply, useBurnedBalance ,useTokenTotalSupply} from 'hooks/useTokenBalance'
import { useFarms, usePriceCakeBusd,usePriceMuftBusd ,useTotalValue} from 'state/hooks'

import styled from 'styled-components'
import { Card, CardBody, Heading, Skeleton, Text } from '@pancakeswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import { useGetStats } from 'hooks/api'
import CardValue from './CardValue'
import { Container } from './CakeStats'

const StyledTotalValueLockedCard = styled(Card)`
  align-items: center;
  display: flex;
  // flex: 1;
`


const TotalValueLockedCard = () => {
  const TranslateString = useI18n()
  // const data = useGetStats()
  const totalValue = useTotalValue();
  // const tvl = totalValue.toFixed(2);
  const totalMUFTSupply = useTokenTotalSupply("0x1d4a26a9d980afc2287dca15eb896a26384bac7c")
  const totalMSWAPSupply = useTokenTotalSupply("0x9393d3C08956F245cdEE9ac9DD7214131Ae2bB8B")
  const MuftPrice = usePriceMuftBusd();
  const MSWAPPrice = usePriceCakeBusd();

  const [mswapMarketCap,setMSwapMarketingCap] = useState(0);
  const [muftMarketCap,setMuftMarketingCap] = useState(0);


  useEffect(()=>{
    if(totalMUFTSupply &&MuftPrice){
      const sup = totalMUFTSupply.toNumber()/1e18;
      const marketingCap = MuftPrice.times(sup).toNumber();
      console.log("totalMUFTSupply",marketingCap)
      setMuftMarketingCap(marketingCap)

    }
  },[totalMUFTSupply,MuftPrice])


  useEffect(()=>{
    if(totalMSWAPSupply && MSWAPPrice){
      const sup = totalMSWAPSupply.toNumber()/1e18;

      const marketingCap = MSWAPPrice.times(sup).toNumber();
      setMSwapMarketingCap(marketingCap)

      console.log("mswapReply",marketingCap)
    }

  },[totalMSWAPSupply,MSWAPPrice])

  return (
    <StyledTotalValueLockedCard>
      <CardBody>
        
        <Container style={{flexDirection:"column"}}>
          {/* <Heading size="xl">{`$${tvl}`}</Heading> */}
          <Heading size="lg" mb="24px" style={{margin:"0"}}>
          {TranslateString(999, 'Total Value Locked (TVL)')}
         </Heading>
          {/* <Heading size="xl"> */}
            <CardValue value={totalValue.toNumber()} prefix="$" decimals={2}/>
          {/* </Heading> */}
          {/* <Text color="textSubtle">{TranslateString(999, 'Across all Farms and Pools')}</Text> */}
        </Container>


        <Container style={{flexDirection:"column"}}>
          <Heading size="lg" mb="24px" style={{margin:"0"}}>
          MUFT Market Cap
         </Heading>
          <CardValue value={muftMarketCap} prefix="$" decimals={2}/>
        </Container>

        <Container style={{flexDirection:"column"}}>
          <Heading size="lg" mb="24px" style={{margin:"0"}}>
          MSWAP Market Cap
         </Heading>
         <CardValue value={mswapMarketCap} prefix="$" decimals={2}/>
        </Container>
      </CardBody>
    </StyledTotalValueLockedCard>
  )
}

export default TotalValueLockedCard
